.contactContent {
    background: $color-3;
    border-radius: $borderRadius;
    width: 100%;
    overflow: hidden;
    display: grid;
    grid-template-columns: 1fr;
    grid-template-rows: 20% 45% 35%;
    grid-template-areas:
      "1"
      "2"
      "3";
  
    .header {
      grid-area: 1;
      width: 100%;
      position: relative;
      background: $color-1;
      height: 340px;
  
      &:before {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        background: url(../../../public/media/map.jpg) no-repeat center top/cover;
        filter: grayscale(100%);
      }
      &:after {
        content: '';
        width: 100%;
        height: 100%;
        position: absolute;
        top: 100px;
        background: url(../../../public/media/shape.svg) no-repeat center /cover;
        z-index: 1; // Assurez-vous que shape.svg est derrière la contactBox
      }
    }
    
    .contactBox {
      grid-area: 2;
      background: $color-5;
      margin: 0 auto;
      padding: 1rem 3rem;
      border-radius: $borderRadius;
      box-shadow: 0 3px 10px #333;
      width: 50%;
      min-width: 440px;
      min-height: 200px;
      position: absolute; // Changer de relative à absolute pour un positionnement sur l'image
      top: 150px; // Ajuster la position
      left: 50%; // Centrer horizontalement
      transform: translateX(-50%) translateY(0); // Centrer horizontalement et ajuster la hauteur
      z-index: 2; // S'assurer qu'elle est au-dessus des autres éléments
      display: flex;
      flex-direction: column;
      align-items: center; // Centrer horizontalement les enfants
      justify-content: center; // Centrer verticalement les enfants
  
      h1 {
        text-align: center;
        margin-top: 0.3rem;
        position: relative;
        color: $color-2;
  
        &:after {
          content: '';
          height: 1px;
          width: 50%;
          background: $color-1;
          position: absolute;
          bottom: -25px;
          left: 50%;
          transform: translateX(-50%);
        }
      }
  
      ul {
        width: 100%;
        display: flex;
        flex-direction: column;
        align-items: center;
        margin-top: 3.5rem;
        padding: 0;
  
        li {
          width: 100%;
          position: relative;
          display: flex;
          align-items: center;
          justify-content: center;
          margin-bottom: 1rem;
  
          i {
            font-size: 1.4rem;
            color: $color-2;
            margin-right: 1rem;
          }
  
          span {
            font-size: 1.4rem;
            background: transparent;
            border: none;
            outline: none;
            width: 350px;
            transition: .2s ease;
            text-align: left; // Alignement du texte à gauche
          }
  
          .clickInput:hover {
            transform: translateX(4px);
            cursor: pointer;
          }
        }
      }
    }
  }

  @media screen and (max-width: 500px){
    .contactContent {
      .contactBox {
        min-width: 100% !important;
      }
      span {
        font-size: .9rem !important;
      }
      h4 {
        font-size: .9rem;
      }
    }
  }